button {
  cursor: pointer;
  border: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

a {
  text-decoration: none;
}

body {
  color: var(--mainTextDarkColor);
  background-color: var(--mainTextLightColor);
  font-family: DM Sans, sans-serif;
  font-size: 14px;
  line-height: 1.76;
}

body.modal-open {
  overflow: hidden;
}

html {
  scroll-behavior: smooth;
}

:root {
  --mainTextLightColor: #fff;
  --secondaryTextLightColor: #ffa5ba;
  --mainTextDarkColor: #000;
  --secondaryTextDarkColor: #907e82;
  --brandColor: #d41443;
  --btnPinkColor: #d41443;
  --btnPinkHover: #c7133e;
  --btnLightColor: #fff;
  --btnLightHover: #ecebeb;
  --backgroundHeroColor: #ffb8ca;
  --backgroundLightColor: #e5e5e5;
  --backgroundPinkColor: #fff5f6;
  --aditionalColor1: #f0d1a5;
  --aditionalColor2: #c2e297;
  --svgColor: #fea5bb;
  --mainTransition: cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
