@keyframes moveUp {
  0% {
    transform: scale(.5)translateY(100%);
  }

  100% {
    transform: scale(1)translateY(0%);
  }
}

@keyframes moveUpMobile {
  0% {
    transform: scale(.5)translateY(100%)translateX(-25%);
  }

  100% {
    transform: scale(1)translateY(0%)translateX(-25%);
  }
}

@keyframes scaleCloser {
  0% {
    transform: scale(3)translateX(0%);
  }

  100% {
    transform: scale(1)translateX(0%);
  }
}

@keyframes scaleCloserMobile {
  0% {
    transform: scale(3)translateX(0%);
  }

  100% {
    transform: scale(1)translateX(-40%);
  }
}

